<template>
  <div class="nes-container is-dark with-title main">
    <p class="title">Sinionth Convert String</p>
    <div class="main-body">
      <div class="main-charmander">
        <i class="nes-charmander" @click="convert"></i>
      </div>
      <div class="main-input">
        <textarea 
          v-model="text" 
          class="nes-textarea is-error" 
          placeholder="Type some thing ..."
          @keyup.enter="convert"
        ></textarea>
      </div>
      <div class="main-output">
        <textarea 
          id="output"
          v-model="converted_text" 
          class="nes-textarea is-success" 
          disabled 
          style="background-color:#fff;"
        ></textarea>
      </div>
      <div class="main-bulbasaur">
        <i class="nes-bulbasaur" @click="convert"></i>
      </div>
      <div class="main-function nes-container is-dark">
        <div class="main-function-select">
          <div class="nes-select is-dark">
            <select required id="dark_select" v-model="mode_selected">
              <option :key="index" v-for="(option, index) in modes" v-bind:value="option.value">
                {{ option.text }}
              </option>
            </select>
          </div>
        </div>
        <br>
        <div class="main-function-radio">
          <label :key="index" v-for="(option, index) in mode_radio[mode_selected]">
            <input type="radio" class="nes-radio is-dark" :value="option" v-model="converted_type"/>
            <span>{{option}}</span>
          </label>
        </div>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  name: 'NesStringConvert',
  data() {
    return {
      regex_const: {
        sentence: new RegExp('^[A-Z][a-z ]*$'),
        capitalized: new RegExp('^([A-Z][a-z]* *)+$'),
        lower: new RegExp('^[a-z ]+$'),
        upper: new RegExp('^[A-Z ]+$'),
        camel: new RegExp('^[a-z][A-Za-z]*$'),
        pascal: new RegExp('^([A-Z][a-z]*)+$'),
        snake: new RegExp('^[a-z][a-z_]*$'),
        kebab: new RegExp('^[a-z][a-z-]*$'),
      },
      text: '',
      converted_text: '',
      modes: [
        {text: 'Normal', value: 0},
        {text: 'Python', value: 1},
        {text: 'C#', value: 2},
      ],
      mode_selected: 0,
      mode_radio: [
        [ 
          'Sentence case', 'Capitalized Case', 
          'UPPER CASE', 'lower case',
          'camelCase', 'PascalCase',
          'snake_case', 'kebab-case'
        ],
        [],
        []
      ],
      converted_type: 'Sentence case',
      origin: ''
    }
  },
  methods: {
    clear: function(){
      this.text = ''
    },
    update_origin: function(){
      this.origin = this.text
    },
    reset_origin: function(){
      this.text = this.origin 
    },
    convert(){
      if(this.converted_type === 'Sentence case') this.convert_sentence()
      else if(this.converted_type === 'Capitalized Case') this.convert_capitalized()
      else if(this.converted_type === 'UPPER CASE') this.convert_upper()
      else if(this.converted_type === 'lower case') this.convert_lower()
      else if(this.converted_type === 'camelCase') this.convert_camel()
      else if(this.converted_type === 'PascalCase') this.convert_pascal()
      else if(this.converted_type === 'snake_case') this.convert_snake()
      else if(this.converted_type === 'kebab-case') this.convert_kebab()
    }, 
    pre_convert: function(){
      this.text = this.text.trim()
      if (this.regex_const.upper.test(this.text)) {
        return
      }
      else if (this.regex_const.camel.test(this.text) || this.regex_const.pascal.test(this.text)) {
        var temp_arr = []
        var pnt = 0
        for (var index in this.text) {
          if (this.text.charAt(index) == this.text.charAt(index).toUpperCase()){
            temp_arr.push(this.text.substring(pnt, index))
            pnt = index
          }
        }
        temp_arr.push(this.text.substring(pnt))
        this.text = temp_arr.join(' ').trim()
      }
      else if (this.regex_const.snake.test(this.text)) {
        this.text = this.text.split('_').join(' ')
      }
      else if (this.regex_const.kebab.test(this.text)) {
        this.text = this.text.split('-').join(' ')
      }
    },
    copy_to_clipboard(){
      navigator.clipboard.writeText(this.converted_text)
    },
    convert_sentence: function(){
      this.pre_convert()
      this.converted_text = this.text.charAt(0).toUpperCase() + this.text.substr(1).toLowerCase()
      this.copy_to_clipboard()
    },
    convert_capitalized: function(){
      this.pre_convert()
      var arr_text = this.text.split(' ')
      for (var index in arr_text) {
        arr_text[index] = arr_text[index].charAt(0).toUpperCase() + arr_text[index].substr(1).toLowerCase()
      }
      this.converted_text = arr_text.join(' ')
      this.copy_to_clipboard()
    },
    convert_upper: function(){
      this.pre_convert()
      this.converted_text = this.text.trim().toUpperCase()
      this.copy_to_clipboard()
    },
    convert_lower: function(){
      this.pre_convert()
      this.converted_text = this.text.trim().toLowerCase()
      this.copy_to_clipboard()
    },
    convert_camel: function(){
      this.pre_convert()
      var arr_text = this.text.split(' ')
      for (var index in arr_text) {
        if (index == 0){
          arr_text[index] = arr_text[index].toLowerCase()
        }
        else {
          arr_text[index] = arr_text[index].charAt(0).toUpperCase() + arr_text[index].substr(1).toLowerCase()
        }
      }
      this.converted_text = arr_text.join('')
      this.copy_to_clipboard()
    },
    convert_pascal: function(){
      this.pre_convert()
      var arr_text = this.text.split(' ')
      for (var index in arr_text) {
        arr_text[index] = arr_text[index].charAt(0).toUpperCase() + arr_text[index].substr(1).toLowerCase()
      }
      this.converted_text = arr_text.join('')
      this.copy_to_clipboard()
    },
    convert_snake: function(){
      this.pre_convert()
      this.converted_text = this.text.toLowerCase().split(' ').join('_')
      this.copy_to_clipboard()
    },
    convert_kebab: function(){
      this.pre_convert()
      this.converted_text = this.text.toLowerCase().split(' ').join('-')
      this.copy_to_clipboard()
    },
  }
}
</script>